<template>

    <div>
      <div class="chart-container mb-5">
        <apexchart class="chart" type="pie" :options="chartOptions" :series="series"></apexchart>
      </div>
      <div>
        <v-btn
          id="btn-modal-asistencia"
          color="red lighten-2"
          class="btn-block"
          @click="openModalAsistencia"
          dark
        >Ver Asistencia</v-btn>
        <v-dialog v-model="modalAsistencia" max-width="700">
          <v-card>
            <v-toolbar dark color="#74569E">
              <v-toolbar-title>Asistencia</v-toolbar-title>
              <v-spacer></v-spacer>
              <v-toolbar-items>
                <v-btn icon dark @click="modalAsistencia = false">
                  <v-icon>mdi-close</v-icon>
                </v-btn>
              </v-toolbar-items>
            </v-toolbar>

            <CalendarioAsistencia />
          </v-card>
        </v-dialog>
      </div>
    </div>

</template>

<script>
import VueApexCharts from "vue-apexcharts";
import CalendarioAsistencia from "../Cards/CardAsistencia";

export default {
  components: {
    apexchart: VueApexCharts,
    CalendarioAsistencia
  },
  props: {
    series: Array,
    labels2: Array
  },
  data: () => ({
    /* series: [60, 40], */
    modalAsistencia: false,
    chartOptions: {
      chart: {
        width: 300,
        type: "pie",
        events: {
          dataPointSelection: function(e, chartContext, config) {
            if (config.dataPointIndex === 0 || config.dataPointIndex === 1) {
              let btnModalAsistencia = document.getElementById(
                "btn-modal-asistencia"
              );
              btnModalAsistencia.click();
            }
          }
        }
      },
      legend: {
        position: "bottom",
        fontSize: "14px",
        fontFamily: "Helvetica, Arial",
        horizontalAlign: "center",
        onItemClick: {
          toggleDataSeries: true
        },
        onItemHover: {
          highlightDataSeries: true
        },
        labels: {
          colors: ["#fff"],
          useSeriesColors: false
        }
      },
      tooltip: {
        style: {
          fontSize: "14px",
          fontFamily: undefined
        },
        custom: function({ series, seriesIndex, w }) {
          return (
            '<div class="arrow_box">' +
            "<span>" +
            w.config.labels[seriesIndex] +
            ": " +
            series[seriesIndex] +
            "%" +
            "</span>" +
            "</div>"
          );
        }
      },
      colors: ["#07B2CC", "#B81F33"],
      dataLabels: {
        enabled: true,
        formatter: function(val) {
          return val + "%";
        }
      },
      labels: ["Asistencia", "Inasistencia"],
      responsive: [
        {
          breakpoint: 480,
          options: {
            chart: {
              width: 230
            },
            legend: {
              position: "bottom",
              horizontalAlign: "center"
            }
          }
        }
      ]
    }
  }),

  mounted() {
    /* this.$refs.calendar.checkChange() */
  },
  methods: {
    openModalAsistencia() {
      try {
        console.log("abriendo modal");
        this.modalAsistencia = true;

      } catch (error) {
        
      }
    }
  }
};
</script>

<style>
.chart-container {
  width: auto;
  margin: 0;
  padding: 0;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
}
.chart {
  width: 300px;
}
.arrow_box {
  position: relative;
  background: #555;
  border: 2px solid #000000;
}
.arrow_box:after,
.arrow_box:before {
  right: 100%;
  top: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}
.arrow_box:after {
  border-color: rgba(85, 85, 85, 0);
  border-right-color: #555;
  border-width: 10px;
  margin-top: -10px;
}
.arrow_box:before {
  border-color: rgba(0, 0, 0, 0);
  border-right-color: #000000;
  border-width: 13px;
  margin-top: -13px;
}
.chart .apexcharts-tooltip {
  color: #fff;
  transform: translateX(10px) translateY(10px);
  overflow: visible !important;
  white-space: normal !important;
}

.chart .apexcharts-tooltip span {
  padding: 5px 10px;
  display: inline-block;
}

.my-event {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  border-radius: 2px;
  background-color: #1867c0;
  color: #ffffff;
  border: 1px solid #1867c0;
  font-size: 12px;
  padding: 3px;
  cursor: pointer;
  margin-bottom: 1px;
  left: 4px;
  margin-right: 8px;
  position: relative;
}

.my-event.with-time {
  position: absolute;
  right: 4px;
  margin-right: 0px;
}

@media screen and (max-width: 1300px) {
  .chart {
    width: 230px;
    height: 200px;
  }
}
</style>